@import url(https://fonts.googleapis.com/css?family=Karla:regular,bold,italic);
@import url(https://fonts.googleapis.com/css?family=Lato:regular,bold,italic);
body{margin:0;font-family:'Lato';font-size:16px;line-height:1.5}h1,h2,h3,h4,h5,h6{font-family:'Karla';margin:0;opacity:60}h1{font-size:5rem;margin:1rem 0}h2{font-size:2.25rem;margin:0 0 1rem}.body_container{display:grid;grid-template-rows:10vw 100vh 10vw;grid-gap:1rem}header{position:fixed;top:0;left:0;width:100vw;padding:2vw;z-index:5}article{min-height:100vh}ul{list-style-type:none}.example{background:none}.example-enter-done{background:red}.container_trail{display:flex;flex-direction:column;flex-wrap:wrap;height:100vw;min-height:100vh}.trail{display:flex;flex-direction:column;width:33%;margin:10px}.trail__subtitle{opacity:0.7}.trail__image{max-width:100%;border-radius:2rem}.trail__body{position:relative}.trail__info{position:absolute;top:63%;color:white;right:0;bottom:5px;left:0;padding:1rem;cursor:pointer;background:rgba(0,0,0,0.7);transition:0.2s ease-in-out top,cubic-bezier(0.075, 0.82, 0.165, 1) border-radius;border-radius:0 0 2rem 2rem}.trail__info .trail__details{margin-top:100%;transition:0.25s ease-in margin, 0.1s ease-out margin;display:flex}.trail__info .trail__details_spec{flex:1 1}.trail__info--open{top:0;border-radius:2rem}.trail__info--open .trail__details{margin-top:5%}.trail__toggle__icon{position:absolute;top:1.25rem;right:1.25rem}.page{min-height:100vh;max-width:100vw;display:flex;align-items:center;justify-content:center}.page-content{flex:0 1 auto;min-width:60vw;padding:6rem 3rem;background:rgba(255,255,255,0.6);-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px)}.flex{display:flex}.searchBar label{display:block}.searchBar_box{background:none;outline:1px solid white;padding:1em;width:75%;color:white}.searchBar_btn{padding:1em;cursor:pointer}.section-hero{display:flex;max-width:100vw;overflow-x:hidden}.section-hero_full{min-height:100vh}.section-hero_center{justify-content:center;align-items:center}.section-hero_content{display:flex;flex-direction:column}.section-hero-title{font-size:2rem}.section_fill-white{background:white}.background-image{position:absolute;top:0;right:0;bottom:0;left:0;z-index:-1}.hero{min-height:100vh;position:relative}.hero-content{display:flex;flex-direction:column;padding:4rem 1rem;color:#f8f8f8}.hero-content_title{order:1;opacity:0;margin-top:100%;transition:0.2s ease-in margin, 0.35s ease-in opacity}.hero-content_title-enter-done{margin-top:0;opacity:1}.hero-content_subtitle{order:0;text-transform:uppercase;opacity:0.5}.hero-content_description{order:2;padding-right:45%}.hero-content_options{order:3}.hero-content_options .col-3{border:1px solid white;border-radius:5px;text-align:center;cursor:pointer}.hero_img,.hero_overlay{position:absolute;top:0;right:0;bottom:0;left:0;z-index:-1}.hero_overlay{background:rgba(0,0,0,0.7)}.hero_content{color:white}.hero .container{top:5vh}.hero_options .col-3{border:1px solid white;border-radius:5px;text-align:center;cursor:pointer}.container{width:90%;margin-left:auto;margin-right:auto;position:relative}.container.is-full{height:100vh}@media only screen and (min-width: 33.75em){.container{width:80%}}@media only screen and (min-width: 60em){.container{width:75%;max-width:60rem}}.row{position:relative;width:100%}.row [class^="col"]{float:left;margin:0.5rem 2%;min-height:0.125rem}.row::after{content:"";display:table;clear:both}.col-1,.col-2,.col-3,.col-4,.col-5,.col-6,.col-7,.col-8,.col-9,.col-10,.col-11,.col-12{width:96%}.col-1-sm{width:4.33333%}.col-2-sm{width:12.66667%}.col-3-sm{width:21%}.col-4-sm{width:29.33333%}.col-5-sm{width:37.66667%}.col-6-sm{width:46%}.col-7-sm{width:54.33333%}.col-8-sm{width:62.66667%}.col-9-sm{width:71%}.col-10-sm{width:79.33333%}.col-11-sm{width:87.66667%}.col-12-sm{width:96%}@media only screen and (min-width: 45em){.col-1{width:4.33333%}.col-2{width:12.66667%}.col-3{width:21%}.col-4{width:29.33333%}.col-5{width:37.66667%}.col-6{width:46%}.col-7{width:54.33333%}.col-8{width:62.66667%}.col-9{width:71%}.col-10{width:79.33333%}.col-11{width:87.66667%}.col-12{width:96%}.hidden-sm{display:block}}

